import React from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import CssBaseline from '@material-ui/core/CssBaseline';
import Container from '@material-ui/core/Container';
import Button from '@material-ui/core/Button';
import Grid from '@material-ui/core/Grid';
import Fab from '@material-ui/core/Fab';

import RemoveIcon from '@material-ui/icons/Remove';
import AddIcon from '@material-ui/icons/Add';

import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import MapCell from './muiltiple/MapCell';
import { useStyles } from '../styles/Styles';

const JSONtable = {
    0: {
        0: {
            color: "#666699",
            url: "https://static.thenounproject.com/png/100391-200.png",
        },
        1: {
            color: "#cc9900",
            url: "https://www.restchurch.com/wp-content/uploads/2017/08/homegroup.png",
        },
        2: {
            color: "#669900",
            url: "https://static.thenounproject.com/png/1821512-200.png",
        }
    },
    1: {
        0: {
            color: "#666699",
            url: "https://static.thenounproject.com/png/100391-200.png",
        },
        1: {
            color: "#3399ff",
            url: "https://static.thenounproject.com/png/1685737-200.png",
        },
        2: {
            color: "#666699",
            url: "https://static.thenounproject.com/png/100391-200.png",
        }
    },
    2: {
        0: {
            color: "#666699",
            url: "https://static.thenounproject.com/png/100391-200.png",
        },
        1: {
            color: "#666699",
            url: "https://static.thenounproject.com/png/100391-200.png",
        },
        2: {
            color: "#666699",
            url: "https://static.thenounproject.com/png/100391-200.png",
        }
    },
};

function DreamMap(props) {
    const { lang, themeMode, history } = props;
    const classes = useStyles();
    const muiTheme = createMuiTheme(themeMode);
    const [cellHeight, setcellHeight] = React.useState(25);
    const [cellWidth, setcellWidth] = React.useState(25);

    const sizeUp = () => {
        setcellHeight(cellHeight + 5);
        setcellWidth(cellWidth + 5);
    };

    const sizeDown = () => {
        setcellHeight(cellHeight - 5);
        setcellWidth(cellWidth - 5);
    };

    const createTable = () => {
        let table = [];
        let i = 0;
        let j = 0;

        Object.keys(JSONtable).forEach(row => {
            let rows = [];
            Object.keys(JSONtable[row]).forEach(col => {
                rows.push(
                    <MapCell key={'cell' + i + j}
                        i={i}
                        j={j}
                        cellHeight={cellHeight}
                        cellWidth={cellWidth}
                        url={JSONtable[row][col].url}
                        color={JSONtable[row][col].color}
                    />
                );
                i++;
            })
            table.push(
                <tr key={'row' + j} >
                    {rows}
                </tr>
            );
            j++;
            i = 0;
        });

        return table;
    }

    return (
        <MuiThemeProvider theme={muiTheme}>
            <CssBaseline />
            <div className={classes.root}>
                <Grid className={classes.mainGridContainer}
                    container
                    direction="column"
                    justify="center"
                    alignItems="stretch" >
                    <Grid item xs={10} zeroMinWidth className={classes.mainGridDreamsBodyItem}>
                        <Container
                            style={{
                                paddingTop: '16px',
                                height: '83%',
                                width: '100%',
                                position: 'absolute',
                            }}
                        >
                            <div
                                style={{
                                    position: 'relative',
                                    height: '100%',
                                    width: '100%',
                                    overflow: 'auto',
                                    borderRadius: '4px',
                                }}
                            >
                                <table
                                    style={{
                                        margin: '0 auto',
                                        backgroundColor: 'rgb(196, 188, 78, 0.75)',
                                    }}
                                >
                                    <tbody>
                                        {createTable()}
                                    </tbody>
                                </table>
                            </div>
                        </Container>
                    </Grid>
                    <Grid item xs={1} zeroMinWidth className={classes.mainGridBodyItem}>
                        <div
                            style={{
                                position: 'absolute',
                                left: '50%',
                                top: '50%',
                                transform: 'translate(-50%, -50%)',
                            }}
                        >
                            <Fab
                                size="small"
                                color="primary"
                                aria-label="add"
                                onClick={() => { sizeUp() }}
                            >
                                <AddIcon />
                            </Fab>
                            <Fab
                                size="small"
                                color="primary"
                                aria-label="add"
                                onClick={() => { sizeDown() }}
                            >
                                <RemoveIcon />
                            </Fab>
                        </div>
                    </Grid>
                    <Grid item xs={1} zeroMinWidth className={classes.mainGridBodyItem}>
                        <Grid
                            container
                            direction="row"
                            justify="space-evenly"
                            alignItems="center"
                        >
                            <Grid item xs={6} align="center">
                                <Button
                                    variant="contained"
                                    color="secondary"
                                    className={classes.actionButton}
                                    onClick={() => { history.push("/luciddreams") }}
                                >
                                    {lang.currLang.buttons.close}
                                </Button>
                            </Grid>
                            <Grid item xs={6} />
                        </Grid>
                    </Grid>
                </Grid>
            </div>
        </MuiThemeProvider >
    )
}

DreamMap.propTypes = {
    themeMode: PropTypes.object.isRequired,
    lang: PropTypes.object.isRequired,
};

const mapStateToProps = store => {
    return {
        themeMode: store.themeMode,
        lang: store.lang,
    }
};

const mapDispatchToProps = (dispatch) => {
    return {
    }
}

export default connect(
    mapStateToProps,
    mapDispatchToProps
)(DreamMap);